<template>
  <div class="create">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link>
        <span>/</span>
        <span v-if="$route.path==='/topic/create'">发布话题</span>
        <span v-else>编辑话题</span>
      </template>
      <template #content>
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item label="板块：" label-width="80px" prop="tab">
            <el-select v-model="form.tab" clearable placeholder="请选择">
              <el-option label="分享" value="share"></el-option>
              <el-option label="问答" value="ask"></el-option>
              <el-option label="招聘" value="job"></el-option>
              <el-option label="客户端测试" value="dev"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="标题：" label-width="80px" prop="title">
            <el-input v-model="form.title"></el-input>
          </el-form-item>
          <el-form-item>
            <mavon-editor
              v-model="form.content"
              defaultOpen="edit"
              :subfield="false"
              :boxShadow="false"
            />
          </el-form-item>
          <el-form-item>
            <el-button v-if="$route.path==='/topic/create'" type="primary" @click="create">立即创建</el-button>
            <el-button v-else type="primary" @click="save">保存</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Panel>
    <Aside>
      <template #header>Markdown 语法参考</template>
      <template #content>
        ### 单行的标题
        **粗体**
        `console.log('行内代码')`
        ```js\n code \n``` 标记代码块
        [内容](链接)
        ![文字说明](图片链接)
      </template>
    </Aside>
  </div>
</template>

<script>
import { mapActions,mapState } from "vuex";
import Panel from "../containers/Panel.vue";
import Aside from "../containers/Aside.vue";
export default {
  components: {
    Panel,
    Aside
  },
  computed: {
    ...mapState({
      newPostId:(state)=>state.post.newPostId
    })
  },
  watch: {
    newPostId: {
      handler(newvalue){
        console.log(newvalue)
        this.$router.push(`/post/${this.newPostId}`)
      },
      // immediate:true
    }
  },
  data() {
    return {
      form: {
        title: "",
        tab: "",
        content: ""
      },
      rules: {
        title: [
          {
            required: true,
            message: "请输入标题",
            trigger: "blur"
          },
          {
            min: 6,
            message: "标题字数六字以上",
            trigger: "blur"
          }
        ],
        tab: [
          {
            required: true,
            message: "请选择板块",
            trigger: "change"
          }
        ]
      }
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
    ...mapState({
      post: state => state.post.post,
    })
  },
  created () {
    // 编辑 获取post
    if(this.id){
    this.getPost(this.id);
    console.log(this.post)
    const {title,tab,content} = this.post
    this.form = {title,tab,content}
    };
  },
  methods: {
    ...mapActions(["createPost",'getPost','updatePost']),
    // 创建
    create() {
      this.$refs.form.validate(bool => {
        if (bool) {
          this.createPost(this.form);
          console.log(this.form);
          console.log(this.newPostId)
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 编辑保存
    async save(){
      await this.updatePost({topic_id:this.id,...this.form})
      this.$router.push(`/post/${this.id}`)
    }
  }
};
</script>

<style lang="less">
.create {
  display: flex;
}
</style>